@import "app";
@font-face {
    font-family: 'DBHelvethaicaXMedCondv3_2';
    src: url('fonts/DBHelvethaicaXMedCondv3_2.eot');
    src: url('fonts/DBHelvethaicaXMedCondv3_2.eot') format('embedded-opentype'),
         url('fonts/DBHelvethaicaXMedCondv3_2.woff2') format('woff2'),
         url('fonts/DBHelvethaicaXMedCondv3_2.woff') format('woff'),
         url('fonts/DBHelvethaicaXMedCondv3_2.ttf') format('truetype'),
         url('fonts/DBHelvethaicaXMedCondv3_2.svg#DBHelvethaicaXMedCondv3_2') format('svg');
}
@font-face {
    font-family: 'DBHelvethaicaXBdCondv3_2';
    src: url('fonts/DBHelvethaicaXBdCondv3_2.eot');
    src: url('fonts/DBHelvethaicaXBdCondv3_2.eot') format('embedded-opentype'),
         url('fonts/DBHelvethaicaXBdCondv3_2.woff2') format('woff2'),
         url('fonts/DBHelvethaicaXBdCondv3_2.woff') format('woff'),
         url('fonts/DBHelvethaicaXBdCondv3_2.ttf') format('truetype'),
         url('fonts/DBHelvethaicaXBdCondv3_2.svg#DBHelvethaicaXBdCondv3_2') format('svg');
}


$font_bold : 'DBHelvethaicaXBdCondv3_2';
$font_med : 'DBHelvethaicaXMedCondv3_2';
$font_size_med : 24px;
$red : #EA2227;
$red2 : #ee4f52;
$img_w : 480px;
$easeOutBack : cubic-bezier(0.175, 0.885, 0.320, 1.275);

/*
SHARE CSS ====================================== */
*,*:focus{outline:none;}
.clear,.clearfix:after,.box-btn:after{clear:both; height:0px; overflow:hidden;}
.clearfix:after,.box-btn:after{content: "";display: block;}
.floatL {float:left;}
.floatR {float:right;}
.floatN{float:none;}
.txtL {text-align:left;}
.txtR {text-align:right;}
.txtC{text-align:center;}
.grayImg {
	/*-webkit-filter: grayscale(100%); -moz-filter: grayscale(100%); -o-filter: grayscale(100%); -ms-filter: grayscale(100%); filter: grayscale(100%); */
	filter: url("data:image/svg+xml;utf8,<svg xmlns=\'http://www.w3.org/2000/svg\'><filter id=\'grayscale\'><feColorMatrix type=\'matrix\' values=\'0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0 0 0 1 0\'/></filter></svg>#grayscale"); /* Firefox 3.5+ */
    filter: gray; /* IE6-9 */
    -webkit-filter: grayscale(100%); /* Chrome 19+ & Safari 6+ */
}
/* smooth font */
.smooth-font,.ff{-webkit-font-smoothing: antialiased;}

/* custom color placeholder */
.input ::-webkit-input-placeholder {color: #ccc;}
.input :-moz-placeholder { /* Firefox 18- */color: #ccc;}
.input ::-moz-placeholder {  /* Firefox 19+ */color: #ccc;}
.input :-ms-input-placeholder {color: #ccc;}

/* remove arrow input type number */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {-webkit-appearance: none;margin: 0;}

/* custom color autocomplete */
input:-webkit-autofill {-webkit-box-shadow:0 0 0 50px white inset;-webkit-text-fill-color: #000;}
input:-webkit-autofill:focus {-webkit-box-shadow:0 0 0 50px white inset;-webkit-text-fill-color: #000;}

/* custom input range */
input[type=range]::-webkit-slider-thumb {
  background: #fff;
  width: 15px;height: 15px;
  -webkit-appearance: none;
  border-radius: 50%;
  cursor: pointer;
}
a{
	-webkit-transition:	all 350ms ease;
		-moz-transition:all 350ms ease;
		-ms-transition: all 350ms ease;
			transition:	all 350ms ease;
}
.btn-menu *{
	-webkit-transition:	all 350ms $easeOutBack;
		-moz-transition:all 350ms $easeOutBack;
		-ms-transition: all 350ms $easeOutBack;
			transition:	all 350ms $easeOutBack;
}
.btnSty1:after,
.btnSty1 span{
	-webkit-transition:	background-color 300ms ease;
		-moz-transition:background-color 300ms ease;
		-ms-transition: background-color 300ms ease;
			transition:	background-color 300ms ease;
}
img{
	display: block;max-width: 100%;max-height: 100%;
	-webkit-backface-visibility: hidden;
	-webkit-transform: translateZ(0) scale(1.0, 1.0);
}
.inb{display: inline-block;}
.btnSty1{
	background-color: transparent;
 	position: relative;
 	border: none;
 	cursor: pointer;
 	padding: 0 50px;
 	display: inline-block;
 	text-decoration: none;
 	margin-bottom: 5px;
}
.btnSty1 span{
	font:400 36px/1 $font_bold;
 	position: relative;
 	z-index: 2;
 	color: #fff;
 	display: inline-block;
 	margin-top: 5px;
}
.btnSty1:after{
	background-color: $red;
 	position: absolute;
 	top: 0;left:0;
 	width: 100%;height: 100%;
	content: "";
	display: block;
	z-index: 1;
	-webkit-transform:skew(-30deg);
			transform:skew(-30deg);
	box-shadow: 5px 5px 0 $red2;
}
.btnSty1:hover:after{
	background-color: #BE0F13;
	box-shadow: 5px 5px 0 $red2;
}
.btnSty1:active:after{
	background-color: #BE0F13;
	box-shadow: none;
	-webkit-transform:translate3d(4px,4px,0) skew(-30deg);
			transform:translate3d(4px,4px,0) skew(-30deg);
}
.btnSty1:active span{
	-webkit-transform:translate3d(4px,4px,0);
			transform:translate3d(4px,4px,0);
}
.box-btn{
	vertical-align: top;
	margin: 15px 0;
}
.fixed,
.show-menu{overflow: hidden;}

/*
LAYOUT CSS ====================================== */
html,body{
	min-height: 100vh;
	background-color: #000;
}
body{
	font-family: $font_med;
	color: #fff;
	line-height: 1.4;
	-webkit-font-smoothing: antialiased;
}
p{margin: 15px 0;}
section{
	padding: 15px 0 50px 0;
	@include mobile {
		padding: 15px 0;
	}
}
figure{margin:0;}
.content{min-height:calc(100vh - 90px);overflow: hidden;}
.container{
	width: 100%;
	max-width:800px;
	position: relative;
	padding: 0 20px;
	box-sizing:border-box;
	margin: 0 auto;
}
.row{
	margin-left: -20px;
	margin-right: -20px;
	@include mobile {
		margin-left: 0;
		margin-right: 0;
	}
	@include tablet-only {
		margin-left: 0;
		margin-right: 0;
	}
}
#video{
	transform: rotateY(180deg);
   -webkit-transform:rotateY(180deg); /* Safari and Chrome */
   -moz-transform:rotateY(180deg); /* Firefox */
}
.box-image{position: relative;}
.cropper-modal{background-color: transparent;opacity: 1;}
.cropper-drag-box{z-index: 9;}
.cropper-view-box{outline: none;}


/* header */
.main-header{
	background-color: #4a4a4a;
	box-shadow: 0 0 7px rgba(0,0,0,0.8);
	height: 45px;
	position: relative;
	z-index: 99;
	.container{
		padding: 15px 0 12px 0;
	}
	.logo{
		display: block;
		width: 150px;
		margin: 0 auto;
	}
	.btn-menu{
		position: absolute;
		top: 0;left:-10px;
		z-index: 999;
		width: 45px;height: 45px;
		display: block;
		@include mobile {
			left:10px;
		}
		@include tablet-only {
			left:10px;
		}
		*{
			display: block;
			width: 100%;height: 100%;
			position: absolute;
			top:0;left:0;
		}
		.normal{
			background: url('../img/btn_menu.png') 50% 50% no-repeat;
			background-size: 33px auto;
			z-index: 2;
		}
		.active{
			background: url('../img/ico_close.svg') 50% 50% no-repeat;
			background-size: 22px auto;
			opacity: 0;
			visibility: hidden;
			z-index: 1;
			-webkit-transform:scale(0);
							transform:scale(0);
		}
		&.active .normal{
			background: url('../img/ico_close.svg') 50% 50% no-repeat;
			background-size: 22px auto;
			opacity: 0;
			z-index: 1;
			visibility: hidden;
			-webkit-transform:scale(0);
							transform:scale(0);
		}
		&.active .active{
			background: url('../img/ico_close.svg') 50% 50% no-repeat;
			background-size: 22px auto;
			opacity: 1;
			z-index: 2;
			visibility: visible;
			-webkit-transform:scale(1);
							transform:scale(1);
		}
	}
	.main-nav{
		background-color: rgba(0,0,0,0.95);
		z-index: 1;
		position: fixed;
		top:0;left:0;
		width: 100%;height: 100%;
		display: table;
		vertical-align: middle;
		-webkit-transition:all 500ms ease;
						transition:all 500ms ease;

		opacity: 0;
		visibility: hidden;
		&.show{
			z-index: 998;
			opacity: 1;
			visibility: visible;
			a{
				-webkit-transform:scale(1);
								transform:scale(1);
			}
		}
		.container{
			text-align: center;
			display: table-cell;
			vertical-align: middle;
		}
		a{
			font:400 36px/1 $font_bold;
			color: #fff;
			text-decoration: none;
			text-align: center;
			display: block;
			padding: 10px 0;
			-webkit-transform:scale(0.8);
							transform:scale(0.8);
			&:hover{color:$red;}
			@include mobile {
				font-size: 30px;
			}
		}
	}
}
/* footer */
.main-footer{
	/* background: url('../img/separate.jpg') 50% 50% no-repeat;
	background-size: auto 100%; */
	background-color: #fff;
	text-align: center;
	height: 45px;
	overflow: hidden;
	a{
		display: inline-block;
		height: 25px;
		padding: 10px 0 10px 50px;
		@include mobile {
			padding: 10px 0 10px 30px;
		}
		&:first-child{
			padding: 10px 50px 10px 0;
			@include mobile {
				padding: 10px 30px 10px 0;
			}
		}
	}
}

/* landing */
.hero-img{
	background-color: #fff;
	display: inline-block;
	position: relative;
	margin-top: 50px;
	max-width: 480px;
	@include mobile {
		margin: 20px 20px 0 20px;
	}
	&:before{
		background: url('../img/btn_gallery.png') 0 0 no-repeat;
		display: block;
		width: 74px;height: 63px;
		position: absolute;
		right: 5px;bottom:5px;
		z-index: 2;
		content: "";
	}
}
.intro{
	font:400 26px/1.2 $font_med;
	margin: 20px 0;
	@include mobile {
		font-size: 22px;
		margin-top: 10px;
		display: block;
		br{display: none;}
	}
}


/* activity */
.box-mdf-img{
	background:url("../img/line-shadow.png") 50% 100% no-repeat;
	background-size: 100% auto;
	text-align: center;
	position: relative;
	padding: 0 0 30px 0;
	margin-bottom: 20px;
	@include mobile {
		width:calc(100vw);
		margin-left: -20px;
		padding-bottom: 10px;
		margin-bottom: 10px;
	}
	.mdf-img{
		background: url('../img/bg_transparent.gif') 0 0 repeat;
		width: 100%;height: 100%;
		margin: 0 auto;
		display: block;
		position: relative;
		overflow: hidden;
		.img,.box-drag{
			position: absolute;
			top:0;left:0;
			z-index: 2;
			overflow: hidden;
		}
		.box-drag,.box-drag .drag{width:100%;height: 100%;z-index: 3;cursor: move;}
		.img-browse{
			width:100%;height: 100%;
			opacity: 1;
			position: absolute;
			top:0;left:0;
			z-index: 1;
			&.show{opacity: 1;}
			&:before{
				position: absolute;
				top:0;left:0;
				width: 100%;height: 100%;
				z-index: 2;
				display: block;
				content: "";
				background-size: 100% auto;
				background-position:0 0;
				background-repeat: no-repeat;
			}
			@for $i from 1 through 15 {
				&.sty-#{$i}:before{background-image: url('../img/bike/MSX_picture-Bike_#{$i}.png');}
			}
			#image{
				position: absolute;
				top:0;left:0;
			}
		}
	}
	.box-img-space{
		display: inline-block;
		position: relative;
		&>*:first-child{
			position: absolute;
			top:0;left:0;
			width: 100%;height: 100%;
			display: block;
			z-index: 2;
			/* -webkit-transform-origin:0 0;
							transform-origin:0 0; */
		}
		.img_w{
			width: $img_w;
			display: block;
			position: relative;
			z-index: 1;
		}
	}
}
.btn-img-txt{
	background-color: transparent;
	display: inline-block;
	border: none;
	position: relative;
	min-width: 100px;
	text-align: center;
	cursor:pointer;
	vertical-align: top;
	text-decoration: none;
	padding: 0;
	.img{
		height: 60px;
		display: block;
		img{display: inline-block;}
	}
	.txt{
		font:400 $font_size_med/1 $font_bold;color:#fff;
	}
	&:hover .txt{border-bottom: 1px solid #fff;}
}

[class^="section-title"]{
	font:400 36px/1 $font_bold;
	text-align: left;
	padding-left: 15px;
	position: relative;
	margin: 0 0 15px 0;
	@include mobile {
		font-size: 30px;
	}
	&:before{
		background-color: $red;
		position: absolute;
		width: 3px;height: 25px;
		left: 0;top:3px;
		content: "";
		display: block;
	}
}
.section-title-2{
	background: url('../img/line-shadow.png') 0 100% no-repeat;
	background-size: 100% auto;
	padding-bottom: 20px;
	margin-bottom: 40px;
	@include mobile {
		margin-bottom: 0;
	}
}
.btn-step-select{
	&:hover .btnSty1:after{
		background-color: #BE0F13;
		box-shadow: 5px 5px 0 $red2;
	}
}
.section-select .box-mdf-img{
	a{
		display: block;
		position: relative;
	}
	.btnSty1{
		position: absolute;
		right:20px;bottom: 15px;
		padding: 5px 20px 0 20px;
		z-index: 2;
		span{
			background: url('../img/ico_next.png') 100% 40% no-repeat;
			background-size: 12px auto;
			font-size: 24px;
			padding-right: 20px;
			padding-top: 5px;
			padding-bottom: 5px;
			margin: 0;
		}
	}
}
.select-bike{
	position: relative;
	padding: 0 20px;
	box-sizing:border-box;
	.item{
		background: url('../img/bg_transparent.gif') 0 0 repeat;
		background-size: 16px auto;
		position: relative;display: block;
		-webkit-backface-visibility: hidden;
		&:before{
			position: absolute;
			top:0;left:0;
			width: 100%;height: 100%;
			z-index: 2;
			background-color: rgba(0,0,0,0.45);
			display: block;
			content: "";
			box-sizing:border-box;
			-webkit-backface-visibility: hidden;
		}
		&:hover:before,
		&.active:before,
		&.active:hover:before{background-color: rgba(0,0,0,0);}
		&.active:before{border:2px solid #f00;}
		&.active{box-shadow: 0 0 1px 0 red;}
	}
	.owl-prev,
	.owl-next{
		position: absolute;
		top:0;
		width: 35px;height: 100%;
		text-indent: -9999px;
	}
	.owl-prev{
		background: url('../img/ico_prev.png') 50% 50% no-repeat;
		background-size: 16px auto;
		left: -20px;
	}
	.owl-next{
		background: url('../img/ico_next.png') 50% 50% no-repeat;
		background-size: 16px auto;
		right: -20px;
	}
}
.section-select-browse{
	.btn-browse{
		input{
			position: absolute;
			width: 100%;height: 100%;
			z-index: 1;
			opacity: 0;
			top:0;left:0;
			cursor:pointer;
		}
	}
}
.btn-edit-img{
	display: none;
	width: 280px;
	margin: 15px auto;
	.input-slider{
		display: block;
		width:100%;height: 2px;
		-webkit-appearance: none;
		cursor:pointer;
	}
	h3{
		font:400 $font_size_med/1 $font_bold;
		color: #fff;
		margin: 10px 0 5px 0;
	}
	.box-btn{padding-top: 15px;}
}
.box-webcam{
	overflow: hidden;
	width: $img_w;height:360px;
}
.cropper-center{display: none!important;}


/* terms of use */
.terms-of-use{
	font-size: $font_size_med;margin: 15px 0;padding: 0 0 0 25px;
	@include mobile {
		padding: 0 0 0 20px;
	}
	li{margin-bottom: 15px;list-style: decimal outside;}
}

/* how-to-play */
.how-to-play-img{
	width: 100%;
	max-width: 350px;
	display: inline-block;
	margin: 15px auto;
}

/* gallery */
.gallery{
	width: 100%;
	max-width: 780px;
	margin: 0 auto;
	box-sizing:border-box;
	@include mobile {padding: 0 10px;}
	.item{
		float: left;
		box-sizing:border-box;
		padding: 10px;
		width: 25%;
		@include mobile {
			width: 33.3333%;
			padding: 5px;
		}
		a{
			display: block;
		}
	}
}
.gallery-head{
	background: url('../img/line-shadow.png') 0 100% no-repeat;
	background-size: 100% auto;
	padding-bottom: 20px;
	margin-bottom: 40px;
	position: relative;
	@include mobile {
		margin-bottom: 0;
	}
	.section-title-2{
		background: none;
		padding-bottom: 0;
		margin-bottom: 0;
	}
}
.gallery-sorting{
	position: absolute;
	top:5px;right:5px;
	a{
		display: block;
		height: 28px;
		float: left;
		margin: 0 10px;
		text-align: center;
		position: relative;
		margin-left: -5px;
		img{display: inline-block;margin-top: 5px;margin-left: 5px;}
		span{
			padding: 0 5px;
			display: inline-block;
			position: relative;
			z-index: 2;
			color: #ccc;
			opacity: 0.5;
		}
		.txt{padding: 5px 10px;}
		&:after{
			background-color: #4A4A4A;
			z-index: 1;position: absolute;
			top:0;left:0;
			width: 100%;height: 100%;
			content: "";
			display: block;
			-webkit-transform:skew(-35deg);
							transform:skew(-35deg);
		}
		&.active span,&:hover span{opacity: 1;}
		&.active .txt,&:hover .txt{color: #fff;}
		&.active:after{background-color: $red;}
	}
}
.box-go-activity{
	background: url('../img/line-shadow-2.png') 50% 0 no-repeat;
	background-size: 100% auto;
	padding-top: 30px;
}
.btn-load-more{
	font:400 24px/1 $font_bold;
	color: #fff;
	border-bottom:1px solid #fff;
	text-decoration: none;
	text-transform: uppercase;
	display: inline-block;
}
.fancybox-title {
	background: url('../img/bg_fb.png') left top no-repeat #fff;
	font: 400 $font_size_med/1 $font_bold!important;
	padding: 4px 10px 0 65px;
	height: 35px;
	box-sizing:border-box;
	text-overflow:ellipsis;
	white-space: nowrap;
}
.fancybox-nav {@include mobile {opacity: 1!important;}}
.fancybox-prev {
	@include mobile {
		background: url('../img/ico_prev-black.png') 50% 50% no-repeat!important;
		background-size: 25px auto!important;
		left: 0!important;
	}
}
.fancybox-next {
	@include mobile {
		background: url('../img/ico_next-black.png') 50% 50% no-repeat!important;
		background-size: 25px auto!important;
		right: 0!important;
	}
}

/*
Loading CSS ====================================== */
/* popup loading */
.popup-loading{width: 300px;text-align: center;}
.popup-loading .popup-inner{min-height:auto;background-color: transparent;}
.popup-loading .box-loading .txt{color: #fff;font-size: 24px;margin-bottom: 15px;display: inline-block;}
.fancybox-loading-skin.fancybox-opened .fancybox-skin{background-color: transparent;box-shadow: none;}
.fancybox-loading .fancybox-overlay:after{display: none;}

