/* smooth font */
.smooth-font, .ff {
  -webkit-font-smoothing: antialiased; }

/* custom color placeholder */
.input ::-webkit-input-placeholder {
  color: #ccc; }

.input :-moz-placeholder {
  /* Firefox 18- */
  color: #ccc; }

.input ::-moz-placeholder {
  /* Firefox 19+ */
  color: #ccc; }

.input :-ms-input-placeholder {
  color: #ccc; }

/* remove arrow input type number */
