  -webkit-box-shadow: 0 0 0 50px white inset;
  -webkit-text-fill-color: #000; }

input:-webkit-autofill:focus {
  -webkit-box-shadow: 0 0 0 50px white inset;
  -webkit-text-fill-color: #000; }

/* custom input range */
input[type=range]::-webkit-slider-thumb {
  background: #fff;
  width: 15px;
  height: 15px;
